#full-menu {
  display: block;
  background: {
    color: $color-primary; }
  width: 100%;
  z-index: 9996;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  overflow: hidden;

  &.not-active {
    @include transition(all .6s);
    height: 0;
    background: {
      color: $color-secondary; }

    ul {
      @for $i from 1 through 5 {
        li:nth-child(#{$i}) {
          @include transition(left .2s #{.30 - ($i * .05)}s);
          left: -100%; } } } }

  &.active {
    @include transition(all .4s);
    height: 100%;

    ul {
      @for $i from 1 through 5 {
        li:nth-child(#{$i}) {
          @include transition(left .1s #{($i * .1)}s);
          left: 0; } } } }

  ul {
    margin: {
      top: 150px; }
    width: 100%;
    position: absolute;

    @media #{$xsmall-max} {
      margin: {
        top: 90px; } }

    li {
      text-transform: uppercase;
      position: relative;
      padding: 15px;
      width: 50%;
      margin: 0 auto;
      font: {
        weight: 800;
        size: 1.2em; }

      @media #{$xsmall-max} {
        width: 90%;
        padding: 5px; }

      span {
        display: block;
        margin: 10px auto;
        width: 0;
        height: 2px; }

      a {
        color: $color-secondary; }

      &:hover {
        a {
          @include transition(color .2s);
          color: $color-special; }

        span {
          @include transition(width .2s);
          width: 40%;
          background: {
            color: $color-special; } } } } } }

@keyframes slide-down {
  0% {
    height: 0; }
  100% {
    height: 100%; } }
