#skills {
  @extend %page;

  @media #{$small-max} {
    margin: 0;
    padding: 0; }

  h1 {
    @extend %h1; }

  p {
    margin: 40px 0; }

  .buttons {
    position: relative;
    width: 300px;
    height: 48px;

    .prev,
    .next {
      cursor: pointer;
      width: 50px;
      height: 50px;
      position: absolute;
      top: 0;
      background: {
        image: url('../images/prev-circle.png'); } }

    .prev {
      left: 0;
      background: {
        image: url('../images/prev-circle.png'); } }

    .next {
      right: 0;
      background: {
        image: url('../images/next-circle.png'); } }

    .line {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      width: 200px;
      left: 50px;
      height: 3px;
      background: {
        color: $color-dark; } } }

  .skill {
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 0 40px;

    @media #{$small-max} {
      padding: 0 15px; }

    &:nth-child(odd) {
      background: {
        color: $color-primary-soft; } }

    .call {
      @include transition(left .2s);
      position: relative;
      left: -100%;
      margin: {
        top: 50px;
        bottom: 50px; }

      &.active {
        left: 0; } }

    .text {
      @include transition(right .3s);
      position: relative;
      right: -100%;
      margin: {
        top: 50px;
        bottom: 50px; }

      &.active {
        right: 0; } }

    ul {
      text-align: center;
      width: 100%;

      li {
        @include transition(all .3s);
        @include border-radius(30px);
        font: {
          weight: 300;
          size: .8em; }
        display: inline-block;
        min-width: 110px;
        margin: 5px;
        padding: 10px;
        background: {
          color: $color-primary; }
        border: 1px solid $color-primary;
        color: $color-secondary;

        &:hover {
          background: {
            color: $color-special; }
          color: $color-soft; }

        i {
          margin: {
            right: 5px; } } } } } }
